.form
    padding: 100px 0
    background: #EEEFF1
    min-height: 760px
    &__forms
        width: 540px
        margin: 0 auto
        &__content
            margin: 0 auto
    &__text
        color: #5F5D5D
        &-int
            padding: 20px 0
            padding-left: 20px
            padding-right: 160px
    &-int
        width: 540px
        margin: 0 auto
        display: flex
        justify-content: space-between
        gap: 20px
        margin-bottom: 30px
    &__btn
        display: block
        margin: 0 auto 
textarea
    width: 720px
    height: 175px
    margin-bottom: 50px
input,
textarea
    cursor: pointer
    &:hover
        border: 1px solid #34547A