.header
    padding: 60px 0 150px 0
    background: $background
    min-height: 700px
    &__top
        margin-bottom: 160px
    &__bottom
        display: flex
        align-items: center
    &__img
        margin-right: 130px
    &__title
        font-weight: 500
        font-size: 48px
        line-height: 79px
        color: $black
        margin-bottom: 60px
    &__subtitle
        line-height: 26px
        margin-bottom: 60px
        width: 515px
.menu
    display: flex
    justify-content: space-between
    align-items: center
    &__list
        display: flex
        gap: 40px
    &__link
        color: $black
        text-transform: uppercase
        transition: .3s
        font-weight: 500
        line-height: 26px
        letter-spacing: 0.1em
        &:hover
            color: $blue