.footer
    padding: 50px 0
    background: #34547A
    &__content
        color: #FFFFFF
        display: flex
        justify-content: space-between
        align-items: center
    &__title
        margin-bottom: 20px
        font-weight: 500
        font-size: 21px
        line-height: 25px
    &__subtitle
        font-size: 13px
        line-height: 21px
    &__icon
        display: flex
        gap: 20px