.skills
    padding: 100px 0
    &__content
        display: flex
        justify-content: space-between
        flex-wrap: wrap
    &__img
        width: 555px
        height: 450px

.skill
    &__title
        text-align: left
    &__subtitle
        line-height: 26px
        color: #727272
        margin-bottom: 20px
    &__bg
        width: 445px
        height: 4px
        background: linear-gradient(to right, #34547A 350px, 10%, #C4C4C4)
        transform: rotate(-0.06deg)
        margin-bottom: 30px

