body
    font-family: 'Roboto', sans-serif
    font-weight: 400
    font-size: 16px
    //color: $text

h1,
h2,
h3,
h4,
h5,
h6
    //font-size: 36px

a,
button 
    // color: $white2

.wrapper 
    width: auto
    margin: 0 auto
.container
    max-width: 1140px
    width: 100%
    padding: 0 15px
    margin: 0 auto

.btn
    background: #34547A
    color: #ffffff
    line-height: 26px
    text-align: center
    letter-spacing: 0.1em
    padding: 25px 40px
    cursor: pointer
    transition: .3s
    text-transform: uppercase
    &:hover
        background: #2e6fbe
.title-h2
    font-weight: 500
    font-size: 32px
    line-height: 38px
    text-align: center
    color: #000000
    margin-bottom: 50px
.text
    width: 515px
    margin: 0 auto 60px
    text-align: center