*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
}

input:focus,
textarea:focus {
  outline: 0;
}

a,
button {
  font-weight: 400;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

button {
  border: 0;
  background: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.wrapper {
  width: auto;
  margin: 0 auto;
}

.container {
  max-width: 1140px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  background: #34547A;
  color: #ffffff;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.1em;
  padding: 25px 40px;
  cursor: pointer;
  transition: 0.3s;
  text-transform: uppercase;
}
.btn:hover {
  background: #2e6fbe;
}

.title-h2 {
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  text-align: center;
  color: #000000;
  margin-bottom: 50px;
}

.text {
  width: 515px;
  margin: 0 auto 60px;
  text-align: center;
}

.header {
  padding: 60px 0 150px 0;
  background: #EEEFF1;
  min-height: 700px;
}
.header__top {
  margin-bottom: 160px;
}
.header__bottom {
  display: flex;
  align-items: center;
}
.header__img {
  margin-right: 130px;
}
.header__title {
  font-weight: 500;
  font-size: 48px;
  line-height: 79px;
  color: #000000;
  margin-bottom: 60px;
}
.header__subtitle {
  line-height: 26px;
  margin-bottom: 60px;
  width: 515px;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__list {
  display: flex;
  gap: 40px;
}
.menu__link {
  color: #000000;
  text-transform: uppercase;
  transition: 0.3s;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 0.1em;
}
.menu__link:hover {
  color: #34547A;
}

.about__content {
  padding: 120px 0 105px 0;
}
.about__subtitle {
  width: 515px;
  text-align: center;
  margin: 0 auto;
  line-height: 26px;
}

.numbers {
  padding: 100px 0;
  background: #34547A;
  width: auto;
}
.numbers__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.numbers__card {
  display: flex;
}
.numbers__img {
  margin-right: 20px;
}
.numbers__title {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 21px;
  line-height: 25px;
  color: #FFFFFF;
}
.numbers__subtitle {
  line-height: 26px;
  color: #FFFFFF;
}

.skills {
  padding: 100px 0;
}
.skills__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.skills__img {
  width: 555px;
  height: 450px;
}

.skill__title {
  text-align: left;
}
.skill__subtitle {
  line-height: 26px;
  color: #727272;
  margin-bottom: 20px;
}
.skill__bg {
  width: 445px;
  height: 4px;
  background: linear-gradient(to right, #34547A 350px, 10%, #C4C4C4);
  transform: rotate(-0.06deg);
  margin-bottom: 30px;
}

.video {
  padding: 110px 0;
  background: #EEEFF1;
  height: 1004px;
}
.video__subtitle {
  width: 515px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 60px;
}
.video__video {
  width: 1110px;
  height: 600px;
}

.project {
  padding-top: 30px;
  padding-bottom: 100px;
}
.project__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 70px;
}
.project__img {
  width: 480px;
}
.project__bottom {
  display: flex;
  gap: 30px;
}
.project__bottom__img {
  width: 255px;
  height: 120px;
}

.form {
  padding: 100px 0;
  background: #EEEFF1;
  min-height: 760px;
}
.form__forms {
  width: 540px;
  margin: 0 auto;
}
.form__forms__content {
  margin: 0 auto;
}
.form__text {
  color: #5F5D5D;
}
.form__text-int {
  padding: 20px 0;
  padding-left: 20px;
  padding-right: 160px;
}
.form-int {
  width: 540px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 30px;
}
.form__btn {
  display: block;
  margin: 0 auto;
}

textarea {
  width: 720px;
  height: 175px;
  margin-bottom: 50px;
}

input,
textarea {
  cursor: pointer;
}
input:hover,
textarea:hover {
  border: 1px solid #34547A;
}

.footer {
  padding: 50px 0;
  background: #34547A;
}
.footer__content {
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__title {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 21px;
  line-height: 25px;
}
.footer__subtitle {
  font-size: 13px;
  line-height: 21px;
}
.footer__icon {
  display: flex;
  gap: 20px;
}